import BaseService from './baseService';
import endpoints from '../constants/endpoints';
import http from '../axios';
import _omit from 'lodash/omit';
import qs from 'query-string';

class RfPointService extends BaseService {
  constructor() {
    super(endpoints.rfPoints);
  }

  fetchAll() {
    let endpoint = endpoints.rfPoints();
    return http.get(endpoint);
  }

  fetch = (query = {}) => {
    const uuid = query.uuid || query.id;

    if (!uuid) {
      throw new Error('UUID is required for fetching points of device');
    }

    let endpoint = endpoints.rfDevices(uuid);
    query = _omit(query, ['uuid', 'id']);

    if (query && Object.keys(query).length > 0) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }

    return http.get(endpoint).then(data => {
      return { dataItems: data.points, extraData: _omit(data, ['points']) };
    });
  };
}

export const rfPointService = new RfPointService();
