<template>
  <v-card outlined :key="renderCount">
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      <span>Points</span><span class="caption pl-3" v-if="deviceName">Device: {{ deviceName }} </span>
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <rf-point-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </rf-point-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import RfPointGrid from './rfPointGrid.vue';
import IconButton from '../../Common/IconButton.vue';
import routeConstant from '../../../constants/routes';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';

const baseHref = routeConstant.rfNetworkDevicePoint;
const initialBreadCrumbItem = {
  text: 'Networks',
  disabled: false,
  link: baseHref,
};

export default {
  name: 'rf-points',
  components: {
    IconButton,
    RfPointGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
        with_points: true,
      },
      renderCount: 0,
      deviceName: '',
      gridData: {},
      breadcrumbItems: [initialBreadCrumbItem],
    };
  },
  watch: {
    '$route.params.deviceId': function (val, oldVal) {
      if (val != oldVal) {
        this.params = { ...this.params, uuid: this.$route.params.deviceId };
        this.renderCount++;
      }
    },
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.deviceId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      if (!(this.gridData && this.gridData.extraData)) {
        return;
      }
      this.deviceName = _get(this.gridData, 'extraData.name', '');
      this.breadcrumbItems = [];
      this.breadcrumbItems.push(initialBreadCrumbItem);
      if (this.gridData.extraData.hasOwnProperty('network_uuid')) {
        const networkUUID = _get(this.gridData, 'extraData.network_uuid', '');
        this.breadcrumbItems.push({
          text: networkUUID,
          align: 'center',
          disabled: false,
          link: routeConstant.rfDevices.replace(':networkId', networkUUID),
        });
      }
      if (this.gridData.extraData.hasOwnProperty('uuid')) {
        this.breadcrumbItems.push({
          text: 'Devices',
          align: 'center',
          disabled: true,
        });

        const deviceUUID = _get(this.gridData, 'extraData.uuid', '');

        this.breadcrumbItems.push({
          text: deviceUUID,
          align: 'center',
          disabled: true,
          link: null,
        });

        this.breadcrumbItems.push({
          text: 'Points',
          align: 'center',
          disabled: true,
        });
      }
    },
  },
};
</script>
